* {
  margin: 0 auto;
  background-color: #000;
}
body {
  padding: 10px 200px;
}
#wrapper {
  text-align: center;
}
.title {
  display: block;
  margin-bottom: 20px;
  color: #fff;
  text-shadow: 2px 2px 7px gray;
}
.info-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.info {
  color: #fff;
  font-weight: bold;
  /* text-align: left; */
}
#moreImg {
  width: 500px;
  height: 300px;
}
.description {
  color: #fff;
  padding: 20px 10px;
}
